.login-max {
  height: 100%;
  background-image: url('../images/login/bg.png');
  position: relative;
}
.login-box {
  width: 456px;
  min-height: 600px;
  background-color: #ffffff;
  box-shadow: 0px 3px 10px 0px rgba(76, 132, 255, 0.2);
  border-radius: 10px;
  position: absolute;
  right: 300px;
  top: 140px;
}
.login-box .login {
  width: 100%;
  height: 100%;
  position: relative;
  padding-bottom: 18px;
}
.login-box .login-wrap {
  display: none;
}
.login-img {
  padding-top: .26rem;
  padding-bottom: .48rem;
  text-align: center;
}
.login-img img {
  display: inline-block;
}
.re {
  position: relative;
  text-align: center;
  margin: 0 auto .22rem;
}
.re {
  position: relative;
  width: 320px;
  height: 48px;
  background-color: #f0f4f9;
  border-radius: 4px;
  border: solid 1px #dee7ef;
  display: flex;
  align-items: center;
  padding: 0 18px;
}
.re input {
  flex: 1;
  border: none;
  background-color: transparent;
  outline: none;
}
.re img {
  margin-right: 11px;
}
input::-webkit-input-placeholder,
input::-webkit-input-placeholder {
  font-family: Source Han Sans CN;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: 0px;
  color: #9aa2b5;
  position: relative;
  text-align: left;
}
.login-login {
  margin-top: 51px;
  margin-left: 68px;
}
.login-btn {
  width: 320px;
  height: 48px;
  outline: none;
  border: none;
  background-color: #77a726;
  border-radius: 4px;
  font-family: Source Han Sans CN;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 31px;
  letter-spacing: 0px;
  color: #ffffff;
}
.login-test {
  margin-top: 20px;
  font-family: Source Han Sans CN;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: center;
  color: #4e5666;
  margin-bottom: 40px;
  cursor: pointer;
}
.login-compute {
  font-family: Source Han Sans CN;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: 0px;
  color: #9aa2b5;
  position: absolute;
  bottom: 18px;
  text-align: center;
  width: 100%;
}
.login-logo-img {
  position: absolute;
  right: 22px;
  top: 10px;
}
.input-wrap:last-child {
  margin-bottom: 0;
}
